.typography-popover-container {
	width: 260px;
	padding: 16px;
	background: #fff;
	border-radius: 8px;
	z-index: 999999;

	//line break
	.typography-popover-selected-options {
		display: flex;
		gap: 12px;
		padding: 10px;
	}

	.typography-spacing-controls {
		display: flex;
		gap: 12px;
		margin-top: 12px;
	}

	.components-toggle-group-control {
		border: 1px solid #ccc;
		border-radius: 4px;
		background: #fff;
		overflow: hidden;

		.et6ln9s1 {
			// This matches your wrapper class
			border-right: 1px solid #e0e0e0;

			&:last-child {
				border-right: none;
			}
		}

		/* Style the buttons inside */
		.components-toggle-group-control-option-base {
			transition: background 0.2s ease;

			&[aria-checked="true"] {
				background: #dfdede;
				font-weight: 600;
			}
		}
	}

	//text transfrom style

	.segmented-control-container {
		border: 1px solid #949494; // Standard WP Border
		border-radius: 2px;
		background: #fff;
		overflow: hidden;
		margin-top: 8px;

		.segment-button.components-button {
			flex: 1;
			height: 32px;
			justify-content: center;
			border: none;
			border-radius: 0;
			padding: 0;
			border-right: 1px solid #e0e0e0;

			&:last-child {
				border-right: none;
			}

			&.is-pressed {
				background: #f0f0f0; // Matches your active state image
				color: #000;
				box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
			}

			&.text-label {
				font-size: 11px;
				font-weight: 500;
			}

			&:hover:not(.is-pressed) {
				background: #f9f9f9;
			}
		}
	}
}
